import {CopyOutlined, SwapOutlined} from '@ant-design/icons';
import {Button, Checkbox, Col, DatePicker, Layout, Row, Select} from 'antd';
import TextArea from 'antd/es/input/TextArea';
import {Content} from 'antd/es/layout/layout';
import {DefaultOptionType} from 'antd/es/select';
import dayjs from 'dayjs';
import {DateTime} from 'luxon';
import {FC, useMemo, useState} from 'react';
import {useNavigate, useParams} from 'react-router-dom';
import tzdata from 'tzdata';

import locale from 'antd/es/date-picker/locale/fr_FR';
import 'dayjs/locale/fr';

import styled from '@emotion/styled';
import customParseFormat from 'dayjs/plugin/customParseFormat';
import localizedFormat from 'dayjs/plugin/localizedFormat';

dayjs.extend(localizedFormat);
dayjs.extend(customParseFormat);

dayjs.locale('fr');

const TitleContainer = styled.div`
  display: flex;
  gap: 10px;
  justify-content: space-between;

  > div {
    position: relative;
    top: 10px;
  }
`;

const TimeZoneSelectContainer = styled.div`
  display: flex;
  gap: 20px;
  justify-content: space-evenly;
  max-width: 600px;
  margin: 20px auto;
`;

const SwapContainer = styled.div`
  display: flex;
  justify-content: center;
  flex-direction: column;

  text-align: center;
  font-family: monospace;

  > div {
    line-height: 0;
    position: relative;
    top: 15px;
  }
`;

const timeZoneOptions: DefaultOptionType[] = Object.entries(tzdata.zones)
  /* .filter(
    ([zoneName, offset]) =>
      Array.isArray(offset) && DateTime.local().setZone(zoneName).isValid,
  ) */
  .map(([zoneName]) => ({
    label: `${zoneName} (${
      DateTime.local().setZone(zoneName).offsetNameShort
    })`,
    value: zoneName,
  }));

const dateRegex = /^\w+ (\d+) (\w+)/;
const timeSlotRegex = /^(\d{2})h(\d{2})/;

const headerDateFormat = 'cccc d LLLL';
const dateFormat = 'yyyy-MM-dd';
const timeFormat = "HH'h'mm";

const dateTimeFormat = "yyyy-MM-dd HH'h'mm";

const getSourceDate = (sourceText: string, zone?: string) => {
  const match = sourceText.trim().match(dateRegex);
  if (!match) return;

  const sourceDate = DateTime.fromFormat(
    `${match[1]} ${match[2]} ${DateTime.now().year}`,
    'd LLLL yyyy',
    {locale: 'fr-FR', zone},
  );

  return sourceDate;
};

const formatDate = (date: DateTime, format: string, forceISO = false) =>
  forceISO ? date.toISO()! : date.toFormat(format, {locale: 'fr'});

const capitalize = (line: string) =>
  line.charAt(0).toUpperCase() + line.slice(1);

export const App: FC = () => {
  const navigate = useNavigate();

  const {sourceTz, targetTz} = useParams();
  const [sourceText, setSourceText] = useState('');

  const [forceISO, setForceISO] = useState(false);
  const [titlesOnly, setTitlesOnly] = useState(false);

  const zoneDiff = useMemo(() => {
    if (!sourceTz || !targetTz) return;

    const diffOfset =
      DateTime.now().setZone(targetTz).offset -
      DateTime.now().setZone(sourceTz).offset;
    return `${diffOfset > 0 ? '+' : ''}${diffOfset / 60}`;
  }, [sourceTz, targetTz]);

  const {sourceDate, targetText} = useMemo(() => {
    const res: {targetText: string; sourceDate?: DateTime} = {targetText: ''};
    if (!sourceText || !sourceTz || !targetTz) return res;

    let sourceDateIterator: DateTime | undefined = undefined;
    let targetDateIterator: DateTime | undefined = undefined;

    for (const line of sourceText.split('\n')) {
      const sourceDate = getSourceDate(line, sourceTz);
      if (sourceDate) {
        if (!res.sourceDate) res.sourceDate = sourceDate;

        if (
          !sourceDateIterator ||
          sourceDateIterator.toISODate() !== sourceDate.toISODate()
        ) {
          sourceDateIterator = sourceDate;
        }

        const targetDate = sourceDate.setZone(targetTz);
        if (
          !targetDateIterator ||
          targetDateIterator.toISODate() !== targetDate.toISODate()
        ) {
          if (!targetDateIterator) {
            res.targetText +=
              capitalize(formatDate(targetDate, headerDateFormat, forceISO)) +
              '\n';
          }

          targetDateIterator = targetDate;

          continue;
        }
      }

      const timeMatch = line.match(timeSlotRegex);
      if (!timeMatch || !sourceDateIterator || !targetDateIterator) {
        if (!titlesOnly) res.targetText += line + '\n';
        continue;
      }

      const sourceDateTime = DateTime.fromFormat(
        `${sourceDateIterator.toFormat(dateFormat)} ${timeMatch[1]}h${
          timeMatch[2]
        }`,
        dateTimeFormat,
        {zone: sourceTz},
      );
      const targetDateTime = sourceDateTime.setZone(targetTz);

      if (
        targetDateIterator.toISODate() !== targetDateTime.toISODate() &&
        targetDateTime.hour >= 6
      ) {
        console.log(targetDateIterator.toISODate(), targetDateTime.toISODate());

        // prevents displaying the wrong date when the day changes on the sourceTz
        if (targetDateIterator.diff(targetDateTime).as('days') < 0) {
          res.targetText +=
            capitalize(formatDate(targetDateTime, headerDateFormat, forceISO)) +
            '\n';
        }

        targetDateIterator = targetDateTime;
      }

      if (!targetDateTime.isValid) {
        if (!titlesOnly) res.targetText += line + '\n';
        continue;
      }

      res.targetText +=
        formatDate(targetDateTime, timeFormat, forceISO) +
        line.substring(timeMatch[0].length) +
        '\n';
    }

    return res;
  }, [sourceText, sourceTz, targetTz, forceISO, titlesOnly]);

  const handleTimeZoneChange = ({
    source = sourceTz ?? '',
    target = targetTz ?? '',
  }: {
    source?: string;
    target?: string;
  }) => {
    navigate(
      '/' + encodeURIComponent(source) + '/' + encodeURIComponent(target),
    );
  };

  return (
    <Layout>
      <Content style={{margin: '0 20px', minHeight: '100vh'}}>
        <TitleContainer>
          <h1>Convertisseur de fuseau horraire</h1>
          <div>
            <label>
              Date dtecte
              <DatePicker
                placeholder="Date dtecte"
                value={sourceDate ? dayjs(sourceDate?.toISO()) : undefined}
                // format={'ddd YYYY'}
                format={'dddd LL'}
                locale={locale}
                disabled
                style={{marginLeft: 10, width: 300}}
                // onChange={(_, date) => setSourceDate(DateTime.fromISO(date))}
              />
            </label>
            <label style={{margin: '0 10px'}}>
              Forcer ISO
              <Checkbox
                checked={forceISO}
                onChange={({target: {checked}}) => setForceISO(checked)}
                style={{marginLeft: 10}}
              />
            </label>
            <label>
              Titres seulement
              <Checkbox
                checked={titlesOnly}
                onChange={({target: {checked}}) => setTitlesOnly(checked)}
                style={{marginLeft: 10}}
              />
            </label>
          </div>
        </TitleContainer>
        <Row gutter={20}>
          <Col span={12}>
            <p>Programme source</p>
            <TextArea
              placeholder="Programme source"
              rows={20}
              value={sourceText}
              onChange={({target: {value}}) => setSourceText(value)}
            />
          </Col>

          <Col span={12}>
            <p>
              Programme cible
              <Button
                disabled={!targetText}
                onClick={() => {
                  navigator.clipboard.writeText(targetText);
                }}
                style={{float: 'right'}}
                size="small"
                icon={<CopyOutlined />}
              >
                Copier
              </Button>
            </p>
            <TextArea
              placeholder="Programme cible"
              rows={20}
              value={targetText}
              disabled
            />
          </Col>
        </Row>
        <TimeZoneSelectContainer>
          <Select
            placeholder="Fuseau source"
            options={timeZoneOptions}
            showSearch
            value={sourceTz}
            onChange={(source) => handleTimeZoneChange({source})}
            style={{width: 250}}
          />
          <SwapContainer>
            <Button
              icon={<SwapOutlined />}
              onClick={() =>
                handleTimeZoneChange({source: targetTz, target: sourceTz})
              }
            />
            <div style={{lineHeight: 0}}>{zoneDiff}</div>
          </SwapContainer>
          <Select
            placeholder="Fuseau cible"
            options={timeZoneOptions}
            showSearch
            value={targetTz}
            onChange={(target) => handleTimeZoneChange({target})}
            style={{width: 250}}
          />
        </TimeZoneSelectContainer>
      </Content>
    </Layout>
  );
};
